import React, { useState } from "react";
import { useInView } from "react-intersection-observer";
import { LazyLoadedObjectCardComponent, DetailsModal } from "components";
import "./ObjectCardGridComponent.css";

const ObjectCardGridComponent = ({ data, ...rest }) => {
  const [ref, inView] = useInView({
    rootMargin: "200px 0px",
  });
  const [detailOverlayData, setDetailOverlay] = useState(null);
  return (
    <div
      ref={ref}
      style={{ minHeight: !inView ? "30000px" : null }}
      className={"Search-Result-Grid"}
    >
      {inView &&
        data.map((id) => (
          <LazyLoadedObjectCardComponent
            className={"object-container"}
            {...rest}
            setDetailOverlay={setDetailOverlay}
            id={id}
            key={id}
          />
        ))}
      {detailOverlayData && (
        <DetailsModal
          item={detailOverlayData}
          closeModal={() => setDetailOverlay(null)}
        />
      )}
    </div>
  );
};

export default ObjectCardGridComponent;
